/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation } from "react-i18next";
import {
  Add,
  StoreOutlined,
  Edit,
  FileCopy as FileCopyIcon,
} from "@material-ui/icons";

/**
  * User imports
  ============================================================================ */
// import Card from "../card";

/**
 * Default export
 */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = { namespace: "Icon" }; // TODO: adjust namespace

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  // const {} = props.barrelGetters;
  // const {} = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
    * Destructured vars
    ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
    * Functions
    ============================================================================ */
  const getJSX = () => {
    return <StoreOutlined style={{ fontSize: "5rem" }} />;
  };

  /**
    * Return
    ============================================================================ */
  return <>{getJSX()}</>;
};
